@charset "UTF-8";
html { box-sizing: border-box; overflow-y: scroll; -webkit-text-size-adjust: 100%; }

*, ::before, ::after { background-repeat: no-repeat; box-sizing: inherit; }

::before, ::after { text-decoration: inherit; vertical-align: inherit; }

* { padding: 0; margin: 0; }

audio:not([controls]) { display: none; height: 0; }

hr { overflow: visible; }

article, aside, details, figcaption, figure, footer, header, main, menu, nav, section, summary { display: block; }

summary { display: list-item; }

small { font-size: 80%; }

[hidden], template { display: none; }

abbr[title] { border-bottom: 1px dotted; text-decoration: none; }

a { background-color: transparent; -webkit-text-decoration-skip: objects; }

a:active, a:hover { outline-width: 0; }

code, kbd, pre, samp { font-family: monospace, monospace; }

b, strong { font-weight: bolder; }

dfn { font-style: italic; }

mark { background-color: #ff0; color: #000; }

sub, sup { font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }

sub { bottom: -0.25em; }

sup { top: -0.5em; }

input { border-radius: 0; }

button, [type="button"], [type="reset"], [type="submit"], [role="button"] { cursor: pointer; }

[disabled] { cursor: default; }

[type="number"] { width: auto; }

[type="search"] { -webkit-appearance: textfield; }

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration { -webkit-appearance: none; }

textarea { overflow: auto; resize: vertical; }

button, input, optgroup, select, textarea { font: inherit; }

optgroup { font-weight: bold; }

button { overflow: visible; }

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner { border-style: 0; padding: 0; }

button:-moz-focusring, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner { outline: 1px dotted ButtonText; }

button, html [type="button"], [type="reset"], [type="submit"] { -webkit-appearance: button; }

button, select { text-transform: none; }

button, input, select, textarea { background-color: transparent; border-style: none; color: inherit; }

select { -moz-appearance: none; -webkit-appearance: none; }

select::-ms-expand { display: none; }

select::-ms-value { color: currentColor; }

legend { border: 0; color: inherit; display: table; max-width: 100%; white-space: normal; }

::-webkit-file-upload-button { -webkit-appearance: button; font: inherit; }

[type="search"] { -webkit-appearance: textfield; outline-offset: -2px; }

img { border-style: none; vertical-align: bottom; }

progress { vertical-align: baseline; }

svg:not(:root) { overflow: hidden; vertical-align: bottom; }

audio, canvas, progress, video { display: inline-block; }

@media screen { [hidden~="screen"] { display: inherit; }
  [hidden~="screen"]:not(:active):not(:focus):not(:target) { position: absolute !important; clip: rect(0 0 0 0) !important; } }

[aria-busy="true"] { cursor: progress; }

[aria-controls] { cursor: pointer; }

[aria-disabled] { cursor: default; }

::-moz-selection { background-color: #b3d4fc; color: #000; text-shadow: none; }

::selection { background-color: #b3d4fc; color: #000; text-shadow: none; }

* { box-sizing: border-box; }

html { font-family: "Hiragino Kaku Gothic ProN", "Meiryo", "メイリオ", sans-serif; }

body { color: #1c1c1c; }

.l-root { position: relative; background-color: #a83d3d; }

.l-header { width: 100%; max-width: 1200px; margin: auto; padding: 16px 16px; display: flex; justify-content: space-between; align-items: center; position: fixed; top: 0; left: 0; right: 0; z-index: 100; }

@media screen and (min-width: 768px) { .l-header { padding: 16px 32px; } }

.l-header__logo { width: 120px; margin-top: -8px; color: inherit; text-decoration: none; transition-duration: 0.1s; }

@media screen and (min-width: 576px) { .l-header__logo { width: 160px; margin-top: 0; } }

@media screen and (min-width: 768px) { .l-header__logo { width: 200px; } }

.l-header__logo:hover { color: #3c3c3c; }

.c-socialLink { width: 112px; display: flex; align-items: center; justify-content: space-between; }

@media screen and (min-width: 576px) { .c-socialLink { width: 128px; } }

.c-socialLink__link { width: 24px; height: 24px; color: inherit; font-size: 24px; line-height: 1; text-align: center; transition-duration: 0.1s; }

.c-socialLink__link[data-brand="twitter"]:hover { color: #1da1f3; }

.c-socialLink__link[data-brand="github"]:hover { color: #171515; }

.c-socialLink__link[data-brand="discord"]:hover { color: #7289da; }
